import React, { useContext, useState , useEffect } from "react";
import ImageGallery from "react-image-gallery";
import "./image-gallery.css";
import { BackButtonPublic } from "../../../../../components/PublicComponents/BackButton/BackButton.jsx";
import { CartContext } from "../../../../../providers/CartContext.js";
import {
  ItemSection,
  ItemWrapper,
  ItemImageGallery,
  ItemContent,
  ItemTitlePriceWrapper,
  ItemTitle,
  ItemPrice,
  ItemSizesWrapper,
  ItemSizeInputLabelWrapper,
  ItemSizeInputLabel,
  ItemSizeInput,
  ItemSizeLabel,
  ItemAddToCartBtn,
  ItemDescribe,
} from "./ItemElement";

const Item = ({ data }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  let selectSize = ""
  const [cart, pushToCart] = useContext(CartContext);
  const imges = [];
  data.images.map((img) => {
    imges.push({
      original: "../" + img,
      thumbnail: "../" + img,
    });
  });

  const addToCartHandler = () => {
    console.log(selectSize);
    if (selectSize !== "") {
      const newItem = {
        ...data,
        size: selectSize,
      };
      return pushToCart(newItem);
    } else {
      alert("Select Size");
    }
  };
  

  return (
    <ItemSection>
      <BackButtonPublic />
      <ItemWrapper>
        <ItemImageGallery>
          <ImageGallery items={imges} showNav={false} />
        </ItemImageGallery>
        <ItemContent>
          <ItemTitlePriceWrapper>
            <ItemTitle>{data.name}</ItemTitle>
            <ItemPrice>
              Price : <strong>{data.price}</strong>
            </ItemPrice>
          </ItemTitlePriceWrapper>
          <ItemSizesWrapper>
            <span>Select Size :</span>

            <ItemSizeInputLabelWrapper>
              {data.sizes.map((size, index) => (
                <ItemSizeInputLabel>
                  <ItemSizeInput
                    type="radio"
                    id={index}
                    name="ItemSize"
                    value={size}
                    onClick={() => {
                      return selectSize = size
                    }}
                  />
                  <ItemSizeLabel for={index}>{size}</ItemSizeLabel>
                </ItemSizeInputLabel>

              ))}
            </ItemSizeInputLabelWrapper>
          </ItemSizesWrapper>
          <ItemAddToCartBtn
            type="submit"
            name="submit"
            value="Add to Card"
            onClick={addToCartHandler}
          />
          <span>
            <strong>Describtion:</strong>
            <ItemDescribe>{data.describe}</ItemDescribe>
          </span>
        </ItemContent>
      </ItemWrapper>
    </ItemSection>
  );
};

export default Item;
