import React, { useEffect } from "react";
import {
  PIP,
  PIPItemsWrapper,
  PIPItemsItem,
  ItemsWrapper,
  ItemsImage,
  ItemsContent,
  ItemsName,
  ItemsPrice,
} from "./ProductItemsPageElement";
import { BackButtonPublic } from "../../../components/PublicComponents/BackButton/BackButton";
import { Link } from "react-router-dom";

const ProductItemsPage = ({ data, key, props }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
    return (
    <>
      <PIP id={data[0].category}>
      <BackButtonPublic />
        <PIPItemsWrapper>
          <PIPItemsItem>
            {data.map((item, index) => (
              <Link
                to={{
                  pathname: item.category + "/"  + item.id,
                  query: { item },
                }}
              >
                <ItemsWrapper>
                  <ItemsImage src={item.images[0]} alt="" />
                  <ItemsContent>
                    <ItemsName>{item.name}</ItemsName>
                    <ItemsPrice>{item.price}</ItemsPrice>
                  </ItemsContent>
                </ItemsWrapper>
              </Link>
            ))}
          </PIPItemsItem>
        </PIPItemsWrapper>
      </PIP>
    </>
  );
};

export default ProductItemsPage;
