import React, { useContext, useState } from "react";
import { useEffect } from "react";
import { BackButtonPublic } from "../../components/PublicComponents/BackButton/BackButton";
import { BackButton } from "../../components/PublicComponents/BackButton/BackButtonElement";
import { BackgroundPng } from "../../components/PublicComponents/BackgroundPng";
import { CartContext } from "../../providers/CartContext";
import {
  CartSection,
  CartWrapper,
  CartItemsWrapper,
  CartItemsTitle,
  CardItems,
  CardItem,
  CardItemSpan,
  CartItemImg,
  CardItemDeleteButton,
  CartContent,
  CardSummary,
} from "./CartElement";

const Cart = () => {
  const [cart, pushToCart, removeItemFromCart] = useContext(CartContext);
  let sum = 0;
  let cartEmpty = true;

  if (cart) {
    cart.map((item) => {
      sum += parseFloat(item.price);
    });
    cartEmpty = false;
  } else {
    cartEmpty = true;
  }

  return (
    <CartSection>
      <BackgroundPng
        src="./assets/images/png/cartBgpng.png"
        width="30%"
        top={false}
        left={false}
        opacity={0.1}
        flipH={true}
      />
      <BackgroundPng
        src="./assets/images/png/shoebox.png"
        width="30%"
        top={true}
        left={true}
        opacity={0.1}
        flipH={false}
      />

      <BackButtonPublic />
      <CartWrapper>
        <CartItemsWrapper>
          <CartItemsTitle>You have {cart.length} items in Cart</CartItemsTitle>
          <CardItems>
            {cartEmpty ? console.log(cartEmpty) : console.log(cartEmpty)}
            {cart.map((item, index) => (
              <CardItem>
                <CartItemImg src={item.images[0]} alt="" />
                <CartContent>
                  <CardItemSpan>{item.name}</CardItemSpan>
                  <CardItemSpan>size : {item.size}</CardItemSpan>
                  <CardItemSpan>Qty : 1</CardItemSpan>
                  <CardItemSpan>price: {item.price}</CardItemSpan>
                </CartContent>
                <CardItemDeleteButton onClick={() => removeItemFromCart(index)}>
                  X
                </CardItemDeleteButton>
              </CardItem>
            ))}
          </CardItems>
        </CartItemsWrapper>
        <div>
          <CartItemsTitle>Cart Summary</CartItemsTitle>
          <CardSummary>
            <CardItemSpan>total numbers: {cart.length}</CardItemSpan>
            <CardItemSpan>total Cost : {sum}$</CardItemSpan>
          </CardSummary>
        </div>
      </CartWrapper>
    </CartSection>
  );
};

export default Cart;
