import React,{useContext} from "react";
import { MobileMenuContext } from "../../providers/MobileMenuContext";
import {
  Nav,
  NavWrapper,
  NavWrapperLogo,
  Logo,
  Title,
  NavCart,
  NavCartMobile,
  NavCartImage,
  NavMenuWrapper,
  NavMenu,
  NavMenuItem,
  SignButton,
  NavMobileWrapper,
  MobileIcon,
} from "./NavElement";

const NavBar = () => {
  const [ navMobileMenu, toggleMobileMenu] = useContext(MobileMenuContext);
  return (
    <Nav className="nav">
      <NavWrapper>
        <NavWrapperLogo href="/">
          <Logo src="./assets/images/logo.svg" alt="logo" />
          <Title>ShoesStore</Title>
        </NavWrapperLogo>

        <NavMenuWrapper>
          <NavMenu>
            <NavMenuItem to="/aboutus">About us</NavMenuItem>
            <NavMenuItem to="/products">Products</NavMenuItem>
            <NavMenuItem to="/contactus">Contact us</NavMenuItem>
            <SignButton to="/signin">Sign up/in</SignButton>
            <NavCart to="/cart">
              <NavCartImage src="../assets/images/png/cart.png" />
            </NavCart>
          </NavMenu>
        </NavMenuWrapper>
        <NavMobileWrapper>
          <NavCartMobile to="/cart" mobileMenu={true}>
            <NavCartImage src="../assets/images/png/cart.png" />
          </NavCartMobile>
          <MobileIcon onClick={toggleMobileMenu} />
        </NavMobileWrapper>
      </NavWrapper>
    </Nav>
  );
};

export default NavBar;
