import React, { useContext } from "react";
import styled from "styled-components";
import ProductItemsPage from "./ProductItemsPage";
import { ProductsContext } from "../../../providers/ProductsContext";
const ProductsApp = (props) => {
 
  const  catg  = props.match.params;
  const [data] = useContext(ProductsContext);
const items = data.items.filter(item => item.category = catg.category)
  return <ProductItemsPage data={items} props={props} />
};

export default ProductsApp;



export const ProductsAppWrapper = styled.div`
position: relative;

`;